@import url(https://fonts.googleapis.com/css2?family=Lato:wght@100;700&family=Open+Sans:wght@400;700&display=swap);
*,*:before,*:after{box-sizing:border-box}html,body,div,span,object,iframe,figure,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,code,em,img,small,strike,strong,sub,sup,tt,b,u,i,ol,ul,li,fieldset,form,label,table,caption,tbody,tfoot,thead,tr,th,td,main,canvas,embed,footer,header,nav,section,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline;text-rendering:optimizeLegibility;-webkit-font-smoothing:antialiased;-webkit-text-size-adjust:none;-moz-text-size-adjust:none;-ms-text-size-adjust:none;text-size-adjust:none}html,body{height:100%}footer,header,nav,section,main{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}input{border-radius:0}body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.spinner{width:40px;height:40px;position:relative;margin:100px auto}.double-bounce1,.double-bounce2{width:100%;height:100%;border-radius:50%;background-color:#333;opacity:0.6;position:absolute;top:0;left:0;-webkit-animation:sk-bounce 2s infinite ease-in-out;animation:sk-bounce 2s infinite ease-in-out}.double-bounce2{-webkit-animation-delay:-1s;animation-delay:-1s}@-webkit-keyframes sk-bounce{0%,100%{-webkit-transform:scale(0)}50%{-webkit-transform:scale(1)}}@keyframes sk-bounce{0%,100%{transform:scale(0);-webkit-transform:scale(0)}50%{transform:scale(1);-webkit-transform:scale(1)}}.container{height:100%}.show-filters{width:100%;font-family:'Open Sans';font-size:0.57em;font-weight:bold;text-transform:uppercase;letter-spacing:0.7px}@media (min-width: 890px){.show-filters{display:none}}.filter-panel{display:none;background-color:#fff;padding:20px 50px;flex-direction:column;justify-content:space-between}.filter-panel.show{display:flex}@media (min-width: 890px){.filter-panel{display:flex;flex-direction:row;height:20vh;min-height:150px}}.filter-panel .checkbox-groups{display:flex;justify-content:space-evenly}.filter-panel .sliders{display:flex;flex-direction:column}.filter-panel .sliders .slider{width:250px;align-self:center}.filter-panel .sliders p{font-family:'Open Sans';font-size:0.57em;font-weight:bold;text-transform:uppercase;letter-spacing:0.7px}.filter-panel .sliders span{font-family:'Open Sans';font-size:0.75em;letter-spacing:0.7px}.results-panel{height:100%}.list-and-details{height:100%;display:block}@media (min-width: 600px){.list-and-details{display:flex;flex-basis:100%}}.error{display:flex;justify-content:space-between;padding:20px;background-color:#f44336;color:white}.error .closebtn{background-color:transparent;border:none;outline:none;color:white;cursor:pointer}

.results-list{height:100%;background-color:#e0e0e0;overflow-y:scroll;font-family:'Open Sans', Arial, Helvetica, sans-serif;min-width:250px;flex-basis:100%}@media (min-width: 700px){.results-list{flex-basis:25%}}.results-list h2{font-size:0.57em;font-weight:bold;text-transform:uppercase;letter-spacing:0.7px;padding-left:50px;margin-top:23px;margin-bottom:15px}.results-list ul{list-style-type:none}.results-list ul li{border-top:1px solid lightgray;padding:10px 50px;font-size:0.8em;line-height:19px}.results-list ul li:hover,.results-list ul li:active,.results-list ul li:focus,.results-list ul li.selected{background-color:lightgray;cursor:pointer}.results-list ul li .quick-bits{font-size:0.8em;line-height:14px}

.results-details-container{height:100%;display:none;background-color:#ededed;width:80%;padding:50px}@media (max-width: 600px){.results-details-container.show{display:flex;position:fixed;width:100%;height:100%;top:0;left:0;right:0;bottom:0}}@media (min-width: 600px){.results-details-container{display:flex}}.details-container{flex-wrap:wrap}@media (min-width: 800px){.details-container{display:flex;flex-wrap:nowrap}}.image{width:275px;height:275px;margin-right:50px}header .title{font-family:'Lato', sans-serif;font-weight:bold;font-size:2em}header .byline{font-family:'Open Sans', sans-serif;font-size:0.8em;margin:5px 0 20px 0}.notables{font-family:'Open Sans', sans-serif;font-size:0.8em;margin-bottom:20px}.notables svg{font-size:1.3em;margin-right:10px}.notables svg[data-icon='check']{color:#50bf55}.notables svg[data-icon='times']{color:#bf5050}.details{font-family:'Open Sans', sans-serif}.details dt{font-size:0.75em;font-weight:bold;text-transform:uppercase;line-height:20px}.details dd{font-size:1.1em;margin:0 0 20px 0}.close-details{display:block}@media (min-width: 600px){.close-details{display:none}}

.cb-container{margin-right:50px;margin-bottom:20px;height:100%;font-size:0.57em}.input-container{display:flex;flex-direction:column;flex-wrap:wrap;height:100%}fieldset{font-family:'Open Sans', sans-serif;font-size:0.57em}legend{font-weight:bold;text-transform:uppercase;letter-spacing:0.7px;margin-bottom:10px}label{margin-right:50px}input[type='checkbox']{background-color:#e5e5e5;outline:none}

